.header {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 100;
	width: 100%;
	pointer-events: none;
	&__inner {
		position: relative;
		z-index: 1; }
	&__overlay {
		position: absolute;
		top: 0;
		left: 0;
		right: 17px;
		height: 120px;
		background-color: $white; } }
.logos {
	float: left;
	width: 226px;
	overflow: hidden;
	pointer-events: initial;
	&__inner {
		height: 120px;
		width: 250px;
		overflow: auto; }
	&__wrap {
		position: relative;
		width: 226px; } }
.full-logo {
	position: absolute;
	top: 215px;
	left: 0;
	font-size: 0;
	&.is-fixed {
		position: fixed;
		top: 30px;
		left: 0;
		width: 100%; }
	&__inner {
		.is-fixed.full-logo & {
			margin: 0 auto;
			max-width: 1070px;
			@include clr; } } }
.logo-m {
	position: relative;
	margin-left: 2px;
	width: 227px;
	overflow: hidden;
	display: block;
	@include clr;
	img {
		display: block; }
	&__symbol {
		float: left;
		position: relative;
		z-index: 1;
		display: block;
		background-color: $white;
		img {
			max-width: 100%;
			width: 42px; } }
	&__musictown,
	&__records {
		position: absolute;
		left: 57px;
		transform: translateX(-227px);
		opacity: 0; }
	&__musictown {
		top: 7px;
		width: 134px; }
	&__records {
		top: 28px;
		width: 104px; } }

.logo {
	position: absolute;
	top: 30px;
	width: 50px;
	@include sm {
		width: 36px; }
	img {
		max-width: 100%;
		display: block; } }
.navigation {
	float: right;
	margin-top: 45px;
	pointer-events: initial;
	@include md {
		position: absolute;
		top: 0;
		right: -200px;
		margin-top: 0;
		padding: 100px 15px 15px;
		width: 200px;
		height: 100vh;
		z-index: 100;
		text-align: right;
		box-shadow: 0 15px 15px transparent;
		transition: transform .35s ease-in-out, box-shadow .35s ease-in-out;
		background-color: $white; }
	.is-open-menu & {
		@include md {
			transform: translateX(-200px);
			box-shadow: 0 15px 15px rgba($darkBlue, 0.2); } } }
.menu {
	font-weight: 700;
	font-size: 0;
	&__item {
		font-size: 16px;
		vertical-align: top;
		display: inline-block;
		@include md {
			display: block; }
		&:not(:first-child) {
			margin-left: 40px;
			@include md {
				margin: 10px 0 0; } } }
	&__link {
		color: $darkBlue;
		transition: border-color .35s ease-in-out;
		border-bottom: 1px solid transparent;
		@include hover {
			border-bottom-color: currentColor; } } }

.btn-menu {
	position: relative;
	float: right;
	margin: 28px -15px 0 0;
	width: 52px;
	height: 56px;
	background: none;
	cursor: pointer;
	pointer-events: initial;
	z-index: 101;
	display: none;
	@include sm {
		margin: 21px -15px; }
	@include md {
		display: block; }
	.header.is-open-menu & {
		&:before {
			transform: translate3d(3px,0,0) rotate(45deg); }
		&:after {
			transform: translate3d(3px,0,0) rotate(-45deg); }
		span {
			opacity: 0;
			transform: scaleX(0.5); } }
	span,
	&:before,
	&:after {
		position: absolute;
		top: 50%;
		left: 50%;
		margin-left: -10px;
		border-top: 2px solid $darkBlue;
		width: 20px; }
	&:before,
	&:after {
		content: '';
		transition: transform 0.3s; }
	span {
		margin-top: -1px;
		transition: opacity 0.3s, transform 0.3s;
		transform-origin: left center; }
	&:before {
		margin-top: -8px;
		transform-origin: left top; }
	&:after {
		margin-top: 6px;
		transform-origin: left bottom; } }
