//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

// Avenir
@font-face {
	@include fontdefault(normal, 900, 'Roboto');
	@include fontpath('Roboto black', 'Robotoblack', Robotoblack); }
@font-face {
	@include fontdefault(italic, 900, 'Roboto');
	@include fontpath('Roboto black italic', 'Robotoblackitalic', Robotoblackitalic); }

@font-face {
	@include fontdefault(normal, 700, 'Roboto');
	@include fontpath('Roboto bold', 'Robotobold', Robotobold); }
@font-face {
	@include fontdefault(italic, 700, 'Roboto');
	@include fontpath('Roboto bold italic', 'Robotobolditalic', Robotobolditalic); }

@font-face {
	@include fontdefault(normal, 500, 'Roboto');
