.icon-arrow-right { width: 1.36em; height: 1em; fill: currentColor; }

.icon-facebook { width: 0.53em; height: 1em; fill: currentColor; }

.icon-next { width: 1em; height: 1em; fill: currentColor; }

.icon-pause { width: 0.75em; height: 1em; fill: currentColor; }

.icon-play { width: 0.89em; height: 1em; fill: currentColor; }

.icon-prev { width: 1em; height: 1em; fill: currentColor; }

/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

body { min-width: 320px; font-family: "Roboto", "Open Sans", sans-serif; font-size: 16px; font-weight: 400; line-height: 1.43; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; background-color: #fff; }

@media only screen and (max-width: 767px) { body { font-size: 14px; } }

.title-lg { font-family: "Roboto", "Open Sans", sans-serif; font-size: 50px; font-weight: 700; line-height: 1.16; letter-spacing: -0.004em; }

@media only screen and (max-width: 767px) { .title-lg { font-size: 24px; } }

.title-md { font-family: "Roboto", "Open Sans", sans-serif; font-size: 30px; font-weight: 700; line-height: 1.16; letter-spacing: 0; }

.out { position: relative; max-height: 100vh; overflow: auto; }

.out__inner { padding-top: 80px; }

.inner { margin: 0 auto; padding: 0 15px; max-width: 1070px; }

.inner:after { content: " "; display: table; clear: both; }

.block { display: flex; }

.header { position: fixed; top: 0; left: 0; z-index: 100; width: 100%; pointer-events: none; }

.header__inner { position: relative; z-index: 1; }

.header__overlay { position: absolute; top: 0; left: 0; right: 17px; height: 120px; background-color: #fff; }

.logos { float: left; width: 226px; overflow: hidden; pointer-events: initial; }

.logos__inner { height: 120px; width: 250px; overflow: auto; }

.logos__wrap { position: relative; width: 226px; }

.full-logo { position: absolute; top: 215px; left: 0; font-size: 0; }

.full-logo.is-fixed { position: fixed; top: 30px; left: 0; width: 100%; }

.is-fixed.full-logo .full-logo__inner { margin: 0 auto; max-width: 1070px; }

.is-fixed.full-logo .full-logo__inner:after { content: " "; display: table; clear: both; }

.logo-m { position: relative; margin-left: 2px; width: 227px; overflow: hidden; display: block; }

.logo-m:after { content: " "; display: table; clear: both; }

.logo-m img { display: block; }

.logo-m__symbol { float: left; position: relative; z-index: 1; display: block; background-color: #fff; }

.logo-m__symbol img { max-width: 100%; width: 42px; }

.logo-m__musictown, .logo-m__records { position: absolute; left: 57px; transform: translateX(-227px); opacity: 0; }

.logo-m__musictown { top: 7px; width: 134px; }

.logo-m__records { top: 28px; width: 104px; }

.logo { position: absolute; top: 30px; width: 50px; }

@media only screen and (max-width: 767px) { .logo { width: 36px; } }

.logo img { max-width: 100%; display: block; }

.navigation { float: right; margin-top: 45px; pointer-events: initial; }

@media only screen and (max-width: 1023px) { .navigation { position: absolute; top: 0; right: -200px; margin-top: 0; padding: 100px 15px 15px; width: 200px; height: 100vh; z-index: 100; text-align: right; box-shadow: 0 15px 15px transparent; transition: transform .35s ease-in-out, box-shadow .35s ease-in-out; background-color: #fff; } }

@media only screen and (max-width: 1023px) { .is-open-menu .navigation { transform: translateX(-200px); box-shadow: 0 15px 15px rgba(13, 42, 99, 0.2); } }

.menu { font-weight: 700; font-size: 0; }

.menu__item { font-size: 16px; vertical-align: top; display: inline-block; }

@media only screen and (max-width: 1023px) { .menu__item { display: block; } }

.menu__item:not(:first-child) { margin-left: 40px; }

@media only screen and (max-width: 1023px) { .menu__item:not(:first-child) { margin: 10px 0 0; } }

.menu__link { color: #0D2A63; transition: border-color .35s ease-in-out; border-bottom: 1px solid transparent; }

.no-touch .menu__link:hover { border-bottom-color: currentColor; }

.btn-menu { position: relative; float: right; margin: 28px -15px 0 0; width: 52px; height: 56px; background: none; cursor: pointer; pointer-events: initial; z-index: 101; display: none; }

@media only screen and (max-width: 767px) { .btn-menu { margin: 21px -15px; } }

@media only screen and (max-width: 1023px) { .btn-menu { display: block; } }

.header.is-open-menu .btn-menu:before { transform: translate3d(3px, 0, 0) rotate(45deg); }

.header.is-open-menu .btn-menu:after { transform: translate3d(3px, 0, 0) rotate(-45deg); }

.header.is-open-menu .btn-menu span { opacity: 0; transform: scaleX(0.5); }

.btn-menu span, .btn-menu:before, .btn-menu:after { position: absolute; top: 50%; left: 50%; margin-left: -10px; border-top: 2px solid #0D2A63; width: 20px; }

.btn-menu:before, .btn-menu:after { content: ''; transition: transform 0.3s; }

.btn-menu span { margin-top: -1px; transition: opacity 0.3s, transform 0.3s; transform-origin: left center; }

.btn-menu:before { margin-top: -8px; transform-origin: left top; }

.btn-menu:after { margin-top: 6px; transform-origin: left bottom; }

.card { position: relative; overflow: hidden; border-radius: 20px 20px 10px 10px; width: 230px; height: 340px; background: #fff; box-shadow: 0 42px 40px -20px rgba(107, 162, 255, 0.26); font-family: "Roboto", "Open Sans", sans-serif; text-align: center; vertical-align: top; display: inline-block; }

.card__cover { height: 230px; }

.card__cover-in { height: 100%; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

.card__content { position: relative; z-index: 2; padding: 42px 15px 20px; }

.card__title, .card__subtitle { font-size: 16px; font-weight: 700; line-height: 1.1875; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }

.card__title.is-active, .card__subtitle.is-active { color: #fff; }

.card__title { margin-bottom: 4px; color: #0D2A63; }

.card__subtitle { color: #5296A7; }

.card__toggle { position: absolute; z-index: 10; left: 20px; bottom: 75px; transform: translate3d(0, 0, 0); }

.card__toggle-button { position: relative; border: 8px solid rgba(255, 255, 255, 0.6); border-radius: 50%; width: 70px; height: 70px; background: #6AA2FF; background-clip: padding-box; text-align: center; font-size: 0; transform: translate3d(0, 0, 0); transition: border-color 0.1s, background 0.15s; }

.card__toggle-button:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

.card__toggle-button.is-active { border-color: rgba(255, 255, 255, 0); }

.card__toggle-button.is-active .card__toggle-play { opacity: 0; }

.card__toggle-button.is-active .card__toggle-pause { opacity: 1; transform: rotate(0); transition: opacity 0.2s 0.2s, transform 0.2s 0.25s; }

.card__toggle-button.is-transparent { background: none; }

.card__toggle-play, .card__toggle-pause { position: absolute; top: 50%; left: 50%; fill: #fff; transition: opacity 0.2s, transform 0.2s; }

.card__toggle-pause { margin: -8px 0 0 -6px; opacity: 0; font-size: 16px; transform: rotate(-45deg); }

.card__toggle-play { margin: -9px 0 0 -8px; font-size: 18px; transform: translate3d(2px, 0, 0); }

.card__overlay { position: absolute; top: 115px; left: 50%; border-radius: 50%; width: 54px; height: 54px; opacity: 0; background: #6AA2FF; box-shadow: 0 0 10px #6AA2FF; transform: translate3d(-50%, -50%, 0); }

.card__overlay.is-active { opacity: 1; }

.card__controls { position: absolute; top: 45px; left: 0; right: 0; }

.card__thumb { margin: 0 auto; border-radius: 50%; width: 140px; height: 140px; opacity: 0.2; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

.card__thumb, .card__control { transform: scale(0); }

.card__control { position: absolute; top: 50%; margin-top: -25px; width: 50px; height: 50px; background: none; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

.card__control-icon { display: inline-block; vertical-align: middle; fill: #fff; }

.card__prev { left: 30px; }

.card__next { right: 30px; }

.card__progressbar { position: absolute; z-index: 1; left: 0; right: 0; bottom: 80px; height: 30px; }

.card__progress-default { position: absolute; top: 50%; left: 0; right: 0; border-bottom: 4px solid rgba(255, 255, 255, 0.4); transform: translate3d(-100%, -50%, 0); }

.card__progress-active { position: absolute; top: 50%; left: 0; right: 20px; margin-top: -2px; height: 4px; opacity: 0; background: #fff; transform: translate3d(-100%, 0, 0); transition: opacity 0.25s; }

.card__progress-active.is-active { opacity: 1; }

.card__progress-active:before { content: ''; position: absolute; top: 50%; left: 100%; margin-top: -10px; border-radius: 50%; width: 20px; height: 20px; background: #fff; box-shadow: -8px 0 7px -4px rgba(0, 0, 0, 0.21); }

.hero { position: relative; padding: 132px 0 245px; overflow: hidden; }

@media only screen and (max-width: 767px) { .hero { padding: 70px 0; } }

.hero__bg { position: absolute; top: 0; left: 0; right: 0; bottom: 0; background-position: 50% 0; background-size: cover; background-repeat: no-repeat; }

.hero__bg:before, .hero__bg:after { position: absolute; content: ""; width: 100vw; height: 50vh; background-color: #fff; }

.hero__bg:before { top: auto; bottom: 0; left: 50%; transform-origin: left bottom; transform: rotate(-172deg); }

.hero__bg:after { top: 100%; right: -50%; transform-origin: top left; transform: rotate(-8deg); }

.slider { color: #fff; height: 371px; opacity: 0; visibility: hidden; transition: opacity .3s ease-in-out, visibility .3s ease-in-out; }

@media only screen and (max-width: 767px) { .slider { height: 273px; } }

.slider.slick-initialized { height: auto; opacity: 1; visibility: visible; }

.slider .slick-dots { font-size: 0; text-align: center; height: 10px; line-height: 10px; }

.slider .slick-dots li { vertical-align: top; display: inline-block; }

.slider .slick-dots li:not(:first-child) { margin-left: 10px; }

.slider .slick-dots li.slick-active button { transform: scale(2); background-color: #fff; }

.slider .slick-dots button { position: relative; width: 5px; height: 5px; font-size: 0; border-radius: 50%; background-color: rgba(255, 255, 255, 0.5); vertical-align: middle; transition: background-color .3s ease-in-out, transform .3s ease-in-out; display: inline-block; }

.slider .slick-dots button:before { position: absolute; content: ""; top: -10px; left: -4px; right: -4px; bottom: -10px; }

.slider__slide { padding-bottom: 87px; }

@media only screen and (max-width: 767px) { .slider__slide { padding-bottom: 55px; } }

.slider__wrap { max-width: 544px; }

.slider__title { margin-bottom: 17px; }

.slider__footer { margin-top: 50px; }

@media only screen and (max-width: 767px) { .slider__footer { margin-top: 30px; } }

.btn { padding: 16px 28px; border-radius: 100px; color: #6AA2FF; font-family: "Roboto", "Open Sans", sans-serif; font-size: 14px; font-weight: 700; line-height: 1.14; letter-spacing: 0; vertical-align: top; box-shadow: 0 22px 10px -10px rgba(50, 79, 128, 0.26); transition: color .35s ease-in-out, background-color .35s ease-in-out, box-shadow .35s ease-in-out; background-color: #fff; display: inline-block; }

.no-touch .btn:hover { color: #fff; box-shadow: 0 22px 10px -10px transparent; background-color: #6AA2FF; }

.btn_blue { color: #fff; border: 1px solid #6AA2FF; box-shadow: none; background-color: #6AA2FF; }

.no-touch .btn_blue:hover { color: #6AA2FF; background-color: #fff; }

.btn .icon { vertical-align: middle; }

.btn .icon-play { margin-right: 10px; font-size: 20px; }

.btn .icon-arrow-right { margin-left: 12px; font-size: 14px; }

.releases { margin-top: -232px; padding-bottom: 108px; }

@media only screen and (max-width: 1084px) { .releases { margin-top: -180px; } }

@media only screen and (max-width: 767px) { .releases { padding: 40px; margin-top: 0; } }

.releases__title { position: relative; color: #fff; margin-bottom: 37px; }

@media only screen and (max-width: 1084px) { .releases__title { text-align: center; } }

@media only screen and (max-width: 767px) { .releases__title { color: #0d2a63; } }

.releases__row { margin: 0 -20px; font-size: 0; text-align: center; }

.releases__card { margin: 0 20px 40px; }

.releases__footer { margin-top: 20px; text-align: center; }
