body {
	min-width: 320px;
	font-family: $roboto;
	@include f(16px, 400, 1.43);
	@include font-smoothing;
	background-color: $white;
	@include sm {
		font-size: 14px; } }

.title-lg {
	font-family: $roboto;
	@include f(50px, 700, 1.16);
	letter-spacing: -0.004em;
	@include sm {
		font-size: 24px; } }

.title-md {
	font-family: $roboto;
	@include f(30px, 700, 1.16);
	letter-spacing: 0; }
